import React from 'react'
import zxcvbn from 'zxcvbn'
import '../css/pwd-strength.css'

class PwdStrength extends React.Component {

    // 使用ES6 class语法创建组件，初始化state的工作要在constructor中完成。不需要再调用getInitialState方法。
    constructor(props) {
        super(props)
        this.state = {
            visible: false,
            value: "",
            isHidePwd: true,
            input: {},
        }
        // 使用ES6 class语法创建组件， class中的方法不会自动将this绑定到实例中。必须使用 .bind(this)或者 箭头函数 ＝>来进行手动绑定。
        this.show = this.show.bind(this);
    }

    componentDidMount() {
        this.input.focus()
    }

    show = () => {
        this.setState({
            visible: true,
        })
    }

    onChange = (event) => {
        this.setState({ value: event.target.value })
    }

    switchInputMode = (event) => {
        // event.preventDefault();
        // console.debug(event.target.type)
        this.setState({ isHidePwd: !this.state.isHidePwd })
    }

    render() {
        const tips_style = ["", "veryweak", "weak", "medium", "strong"];
        const tips_zh_cn = ["事同虚设", "容易猜测", "可预防多数在线攻击", "可预防多数离线攻击", "现有手段难以攻击"];
        const ans = zxcvbn(this.state.value);
        console.debug(tips_zh_cn[ans.score]);
        const score = ans.score;
        console.debug(this.state.isHidePwd);
        // const hideInput = "strength" + (this.state.isHidePwd ? "" : " hide");
        // const showInput = "strength" + (this.state.isHidePwd ? " hide" : "");
        const inputType = this.state.isHidePwd ? "password" : "text";
        const tips = this.state.isHidePwd ? "显示密码" : "隐藏密码";

        return (
            <div className="strength-container">
                <form className="strength-form" onClick={() => { console.debug("只有这里可以搞事情啦(组件input自动聚焦)"); this.input.focus() }}>
                    <label>{this.props.label}</label>
                    {/* className={hideInput} */}
                    <input type={inputType} id="myPassword" name="" defaultValue={this.state.value} className="strength" data-password="myPassword" onChange={this.onChange} ref={(input) => { this.input = input }} />
                    {/* 两个input会涉及ref绑定，状态同步，以及聚焦问题 */}
                    {/* <input type="text" name="" defaultValue={this.state.value} className={showInput} data-password="myPassword" onChange={this.onChange} /> */}
                    <a data-password-button="myPassword" href={document.location.href} className="button_strength" onClick={this.switchInputMode} >{tips}</a>
                    <div className="strength_meter">
                        <div data-meter="myPassword" className={tips_style[score]}>
                            <p>{tips_zh_cn[score]}</p>
                        </div>
                    </div>
                </form>
            </div>
        )
    }
}

export default PwdStrength;
