import {PowerHost} from './PowerHost'

export class PowerMode {
    constructor(nameI, valueI, isCheckI, colorI) {
        this.name = nameI;
        this.color = colorI;
        this.nameMapHost = new Map();
        const buffer = [
            new PowerHost("owner", valueI*100, isCheckI, 'violet'),
            new PowerHost("group", valueI*10, isCheckI, 'red'),
            new PowerHost("other", valueI*1, isCheckI, 'yellow'),
        ];
        buffer.forEach(ele => this.nameMapHost.set(ele.name, ele));
        //console.log(this.nameMapHost);
        //console.log(this.nameMapHost.get('owner'));
    }
    
    toString() {
        return this.name.toString();
    }
}