import React from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import Tabs from '@material-ui/core/Tabs';
import { Link } from "react-router-dom"
import Tab from '@material-ui/core/Tab';
// import Typography from '@material-ui/core/Typography';

const StyledTabs = withStyles({
  indicator: {
    display: 'flex',
    justifyContent: 'center',
    backgroundColor: 'transparent',
    '& > span': {
      maxWidth: 40,
      width: '100%',
      backgroundColor: '#635ee7',
    },
  },
})((props) => <Tabs variant="scrollable" {...props} TabIndicatorProps={{ children: <span /> }} />);

const classes = makeStyles((theme) => ({
  tabLink : {
    display:"flex",
    alignItems:"center",
    justifyContent:"center"
  }
}));

const StyledTab = withStyles((theme) => ({
  root: {
    textTransform: 'none',
    // 导航标签字体颜色
    color: '#282c34',
    fontWeight: theme.typography.fontWeightRegular,
    fontSize: theme.typography.pxToRem(15),
    // marginRight: theme.spacing(1),
    '&:focus': {
      opacity: 1,
    },
  },
}))((props) => <Tab className={classes.tabLink} component={Link} to={props.to}  disableRipple {...props} />);

const useStyles = makeStyles((theme) => ({
  root: {
    margin: 0,
  },
  padding: {
    padding: theme.spacing(0),
  },
  tabs: {
    border:'hidden 1px #5a3b73',
    'border-radius': '75px',
    // backgroundColor: '#d7e0d0',
    backgroundColor: 'rgba(214, 223, 208, 0.6)',
    /* 文字 */
    color: "#282c34",
  },
}));

export default function CustomizedTabs(props) {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <>
        <StyledTabs className={classes.tabs} value={value} onChange={handleChange} aria-label="styled tabs example">
            <StyledTab to="/" label="首页" />
            <StyledTab to="/WalmartLine" label="财务分析图" />
            <StyledTab to="/chmodcalcular" label="权限计算器" />
            <StyledTab to="/components" label="组件" />
            <StyledTab to="/ownservice" label="私有服务" />
            <StyledTab to="/colorselector" label="颜色采集" />
            <StyledTab to="/playgroud" label="练习场" />
            <StyledTab to="/tictactoe" label="井字棋" />
            <StyledTab to="/about" label="关于" />
        </StyledTabs>
        {/* 用于控制上下间隔 */}
      {/* <Typography className={classes.padding} /> */}
    </>
  );
}