// import React, { Component } from 'react';
import React from 'react';
import './TicTacToe.css';
import { FixedSizeList } from 'react-window';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
// import * as ReactDOM from "react-dom";

// @see https://react.docschina.org/tutorial/tutorial.html

function Square(props) {
    return (
        <button className="square" onClick={props.onClick}>
            {props.value}
        </button>
    );
}

class Board extends React.Component {
    renderSquare(i) {
        return (
            <Square
                value={this.props.squares[i]}
                onClick={() => this.props.onClick(i)}
            />
        );
    }

    render() {
        return (
            <div>
                <div className="board-row">
                    {this.renderSquare(0)}
                    {this.renderSquare(1)}
                    {this.renderSquare(2)}
                </div>
                <div className="board-row">
                    {this.renderSquare(3)}
                    {this.renderSquare(4)}
                    {this.renderSquare(5)}
                </div>
                <div className="board-row">
                    {this.renderSquare(6)}
                    {this.renderSquare(7)}
                    {this.renderSquare(8)}
                </div>
            </div>
        );
    }
}

export default class TicTacToe extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            history: [
                {
                    squares: Array(9).fill(null)
                }
            ],
            stepNumber: 0,
            xIsNext: true
        };
    }

    handleClick(i) {
        const history = this.state.history.slice(0, this.state.stepNumber + 1);
        const current = history[history.length - 1];
        const squares = current.squares.slice();
        if (calculateWinner(squares) || squares[i]) {
            return;
        }
        squares[i] = this.state.xIsNext ? "X" : "O";
        this.setState({
            history: history.concat([
                {
                    squares: squares
                }
            ]),
            stepNumber: history.length,
            xIsNext: !this.state.xIsNext
        });
    }

    jumpTo(step) {
        const {history} = this.state;
        this.setState({
            // 清空跃迁后的历史
            history: history.slice(0, step+1),
            stepNumber: step,
            xIsNext: (step % 2) === 0
        });
    }

    render() {
        const history = this.state.history;
        const current = history[this.state.stepNumber];
        const winner = calculateWinner(current.squares);

        // const moves = history.map((step, move) => {
        //     const desc = move ?
        //         '去往第#' + move + '次移动' : '去往游戏开始';
        //     return (
        //         <li key={move}>
        //             <button onClick={() => this.jumpTo(move)}>{desc}</button>
        //         </li>
        //     );
        // });

        let status;
        if (winner) {
            status = "赢家: " + winner;
        } else {
            status = "下一个玩家: " + (this.state.xIsNext ? "X" : "O");
        }

        return (
            <>
                <div style={{ minWidth: '100px' }}>{status}</div>
                <br />
                <div className="game">
                    <div className="game-board">
                        <Board
                            squares={current.squares}
                            onClick={i => this.handleClick(i)}
                        />
                    </div>
                    <div className="game-info">
                        {/* <ol>{moves}</ol> */}
                        <FixedSizeList height={100} style={{ minWidth: '160px' }} itemSize={40} itemCount={history.length}>
                            {
                                (props) => {
                                    const { index, style } = props;
                                    // history[index] 是当时的棋局
                                    // 最后一步在上
                                    let move = history.length - index - 1;
                                    const desc = move ?
                                        '去往#' + move + '次移动' : '去往游戏开始';
                                    return (
                                        <ListItem button key={move} style={style} onClick={() => this.jumpTo(move)}>
                                            <ListItemText primary={desc} />
                                        </ListItem>
                                    );
                                }
                            }
                        </FixedSizeList>
                    </div>
                </div>
            </>
        );
    }
}

// ========================================

function calculateWinner(squares) {
    const lines = [
        [0, 1, 2],
        [3, 4, 5],
        [6, 7, 8],
        [0, 3, 6],
        [1, 4, 7],
        [2, 5, 8],
        [0, 4, 8],
        [2, 4, 6]
    ];
    for (let i = 0; i < lines.length; i++) {
        const [a, b, c] = lines[i];
        if (squares[a] && squares[a] === squares[b] && squares[a] === squares[c]) {
            return squares[a];
        }
    }
    return null;
}
