import React from 'react';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend } from 'recharts';

const data = [
    { name: 'Page A', uv: 4000, pv: 2400, amt: 2400 },
    { name: 'Page B', uv: 3000, pv: 1398, amt: 2210 },
    { name: 'Page C', uv: 2000, pv: 9800, amt: 2290 },
    { name: 'Page D', uv: 2780, pv: 3908, amt: 2000 },
    { name: 'Page E', uv: 1890, pv: 4800, amt: 2181 },
    { name: 'Page F', uv: 2390, pv: 3800, amt: 2500 },
    { name: 'Page G', uv: 3490, pv: 4300, amt: 2100 },
    { name: 'Page H', uv: 4000, pv: 2400, amt: 2400 },
    { name: 'Page I', uv: 3000, pv: 1398, amt: 2210 },
    { name: 'Page J', uv: 2000, pv: 9800, amt: 2290 },
    { name: 'Page K', uv: 2780, pv: 3908, amt: 2000 },
    { name: 'Page L', uv: 1890, pv: 4800, amt: 2181 },
    { name: 'Page M', uv: 2390, pv: 3800, amt: 2500 },
    ...(new Array(20)).fill(
        { name: 'Page N', uv: 3490, pv: 4300, amt: 2100 }
    )
];

class SimpleLineChart extends React.Component {

    // 使用ES6 class语法创建组件，初始化state的工作要在constructor中完成。不需要再调用getInitialState方法。
    constructor(props) {
        super(props);
        this.state = {
            data, 
            left: 0, 
            right: -300
        };
    }

    panLeft = () => {
        const { left, right } = this.state;

        /* You need to figure out the offset number
           based on your dataset range */
        if ((left + 100) >= 50) return;

        this.setState(() => ({
            right: right - 100,
            left: left + 100
        }));
    }

    // 这种方式 this为空? 迷
    // panRight() {
    panRight = () => {
        let { left, right } = this.state;
        /* You need to figure out the offset number
           based on your dataset range */
        if ((right + 100) >= 50) return;

        this.setState(() => ({
            left: left - 100,
            right: right + 100
        }));
    }

    // 这里就行
    render() {
        const { data, left, right } = this.state;

        return (
            <div>
                <button onClick={this.panRight}> Pan Right </button>
                <button onClick={this.panLeft}> Pan Left </button>
                <LineChart width={600} height={300} data={data}
                    margin={{ top: 5, right: 30, left: 20, bottom: 5 }}>
                    <XAxis allowDataOverflow={true} dataKey="name" padding={{ left, right }} />
                    <YAxis yAxisId="left" />
                    <YAxis yAxisId="right" orientation="right" />
                    <CartesianGrid strokeDasharray="3 3" />
                    <Tooltip />
                    <Legend />
                    <Line yAxisId="left" type="monotone" dataKey="pv" stroke="#8884d8" activeDot={{ r: 8 }} />
                    <Line yAxisId="right" type="monotone" dataKey="uv" stroke="#82ca9d" />
                </LineChart>
            </div>
        );
    }
}

export default SimpleLineChart;
