import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TablePagination from '@material-ui/core/TablePagination';
import TableRow from '@material-ui/core/TableRow';
const { isValidVar } = require("utility-js");

const useStyles = makeStyles({
    root: {
        width: '100%',
    },
    container: {
        maxHeight: 440,
    },
});

// CustomTable 自定义表格
// 表头粘连(固定)的表格
export default function StickyHeadTable(props) {
    const classes = useStyles();
    const [page, setPage] = React.useState(0);
    const [rowsPerPage, setRowsPerPage] = React.useState(10);

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(+event.target.value);
        setPage(0);
    };

    if (!isValidVar(props.keygen)) {
        throw Error("props.keygen 用于指定列表中每行的key 必须定义");
    }

    return (
        <Paper className={classes.root}>
            <TableContainer className={classes.container}>
                <Table stickyHeader aria-label="sticky table">
                    <TableHead>
                        <TableRow>
                            {props.columns.map((column, index) => (
                                <TableCell
                                    key={index}
                                    align={column.align}
                                    style={{ minWidth: column.minWidth }}
                                >
                                    {column.label}
                                </TableCell>
                            ))}
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {props.rows.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((row) => {
                            if (!isValidVar(row[props.keygen])) {
                                throw Error("row[props.keygen] 是列表中每行的key 但是该属性是无效的");
                            }
                            return (
                                <TableRow hover role="checkbox" tabIndex={-1} key={row[props.keygen]}>
                                    {props.columns.map((column, index) => {
                                        if (((isValidVar(column.render) ? 1 : 0) + (isValidVar(column.propertyName) ? 1 : 0)) < 1) {
                                            throw new Error("render和propertyName至少需要定义一个");
                                        }
                                        // 只有定义了属性名的情况下会自动取值 此时若定义了render那就使用它没有就直接返回value
                                        const value = isValidVar(column.propertyName) ? row[column.propertyName] : row;
                                        return (
                                            <TableCell key={index} align={column.align}>
                                                {column.render ? column.render(value) : value}
                                            </TableCell>
                                        );
                                    })}
                                </TableRow>
                            );
                        })}
                    </TableBody>
                </Table>
            </TableContainer>
            <TablePagination
                rowsPerPageOptions={[5, 10, 25, 100]}
                component="div"
                count={props.rows.length}
                rowsPerPage={rowsPerPage}
                page={page}
                onChangePage={handleChangePage}
                onChangeRowsPerPage={handleChangeRowsPerPage}
            />
        </Paper>
    );
}