import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import ListItemText from '@material-ui/core/ListItemText';
import PropTypes from 'prop-types';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Box from '@material-ui/core/Box';

import { ListItemLink } from './NestedList';

import { FixedSizeList } from 'react-window';
import { betweenNumLORC } from 'utility-js';
const { isValidVar } = require("utility-js");


// Tab+滚动列表
export function VerticalTabs(props) {
    let renderGroupList = props.renderGroupList;

    const bd = document.querySelector('body');
    const scrWidth = bd.offsetWidth;
    // 高在代码里边获取不到还行
    const scrHeight = bd.offsetHeight;
    console.log("scrWidth" + scrWidth);
    console.log("scrHeight" + scrHeight);
    const rootHeight = 224;
    function TabPanel(props) {
      const { children, value, index, ...other } = props;
  
      return (
        <div
          role="tabpanel"
          hidden={value !== index}
          id={`vertical-tabpanel-${index}`}
          aria-labelledby={`vertical-tab-${index}`}
          {...other}
        >
          {/* value是当前tab的状态 当其与索引值相等时才展示对应的子结点 */}
          {value === index && (
            <Box p={3}>
              {/* 貌似这个会创建一个p组件 这个组件不能作为某些组件的父结点 */}
              {/* <Typography>{children}</Typography> */}
              <div>{children}</div>
            </Box>
          )}
        </div>
      );
    }
  
    TabPanel.propTypes = {
      children: PropTypes.node,
      index: PropTypes.any.isRequired,
      value: PropTypes.any.isRequired,
    };
  
    function a11yProps(index) {
      return {
        id: `vertical-tab-${index}`,
        'aria-controls': `vertical-tabpanel-${index}`,
      };
    }
  
    const useStyles = makeStyles((theme) => ({
      root: {
        flexGrow: 1,
        backgroundColor: theme.palette.background.paper,
        display: 'flex',
        height: rootHeight,
      },
      tabs: {
        borderRight: `1px solid ${theme.palette.divider}`,
      },
    }));
    const classes = useStyles();
    const [value, setValue] = React.useState(0);
  
    const handleChange = (event, newValue) => {
      setValue(newValue);
    };
  
    console.log(betweenNumLORC(1, 3, 4));
    console.log(isValidVar(1));
  
    // function LinkElement(props) {
    //   return (
    //     <TabPanel value={value} index={props.index}>
    //       <Link to={props.href}>{props.name}</Link>
    //     </TabPanel>
    //   )
    // }
  
  
    function clone(origin) {
      return Object.assign({}, origin);
    }
  
    // 左侧有一个侧边栏用于分组，右侧是组内的项目列表
    return (
      <div className={classes.root}>
        <Tabs
          orientation="vertical"
          variant="scrollable"
          value={value}
          onChange={handleChange}
          aria-label="Vertical tabs example"
          className={classes.tabs}
        >
          {
            renderGroupList.map((group, r) => {
              // 隐藏组 后面的tab也要处理（用index做耦合的）但是比较麻烦，所以还是直接删除元素比较好
              // if (isValidVar(group.show_lv) && group.show_lv > curShowLv) {
              //   console.debug(group.name + ":" + group.show_lv);
              //   // renderGroupList.splice(r, 1);
              //   return null;
              // }
              return (
                <Tab key={r} label={group.name} {...a11yProps(r)} />
              )
            })
          }
        </Tabs>
        {
          // renderGroupList.map((group, r) => group.elements.map((ele, c) => (
          //   <LinkElement key={c} href={ele.href} name={ele.name} index={r} />
          // )))
          renderGroupList.map((group, r) => (
            // 用于tab的切换 隐藏效果
            <TabPanel key={r} value={value} index={r}>
              {/* Tab内的定高列表 如果使用普通的 TabPanel数据量大了会溢出 */}
              <FixedSizeList key={r} height={rootHeight - 24} width={scrWidth * 0.5} itemSize={40} itemCount={group.elements.length}>
                {
                  (props) => {
                    // 此处的index和r不是同一个东西
                    const { index, style } = props;
                    let cur_style = clone(style);
                    cur_style.textOverflow = "ellipsis";
                    let ele = group.elements[index];
                    return (
                      <ListItemLink key={index} className="single-line-text-overflow-omitted" style={cur_style} href={ele.href} target={ele.target}>
                        <ListItemText primary={`${ele.name}`} />
                      </ListItemLink>
                    );
                  }
                }
              </FixedSizeList>
            </TabPanel>
          )
          )
        }
      </div>
    );
  }
  