import React from 'react'
import Header from './SailHe-Header.png'
import './about.css'
import '../css/ood.css'
import '../css/dialog-box.css'
import '../css/my-animation.css'
import 'animate.css'

class About extends React.Component {

    // 使用ES6 class语法创建组件，初始化state的工作要在constructor中完成。不需要再调用getInitialState方法。
    constructor(props) {
        super(props)
        this.state = {
            visible: false,
            content: 1,
            animateStep: 1,
        }
        // 使用ES6 class语法创建组件， class中的方法不会自动将this绑定到实例中。必须使用 .bind(this)或者 箭头函数 ＝>来进行手动绑定。
        this.show = this.show.bind(this);
    }

    show = () => {
        this.setState({
            visible: true,
        })
    }

    handleOk = () => {
        this.setState({
            visible: false,
            content: this.state.content + 1,
        })
        console.log('clicked ok!')
    }

    animationend = () => {
        let { animateStep } = this.state;
        console.log('第' + animateStep + '个动画运行结束');
        this.setState({
            animateStep: ++animateStep
        });
    }

    render() {
        return (
            <div className="about-body">
                <div className="center-sail-he flex-center">
                    <img src={Header} className="circle-sail-he" alt="头像" />
                </div>
                {/* <div className="cavern-gate"></div>
                <div className="imaginary-line"></div> */}
                <div style={{ height: "300px" }}>
                    <div className="step-1 main-animation" onAnimationEnd={() => this.animationend()} ></div>
                </div>
                <div className={["step-3", this.state.animateStep === 3 && 'dialog-box-container'].join(' ')}  onAnimationEnd={this.animationend}>
                    {/* <div className={`step-2 dialog-box animation-once animated bounce ${this.state.animateStep===2 ? "active":null}`} onAnimationEnd={() => this.animationend()}>你好</div> */}
                    {/* <div className="step-2 dialog-box animation-once">Hello I'm SailHe, a programmer</div> */}
                    <div className={['step-2', "dialog-box", "animation-once", this.state.animateStep === 2 && 'animate__animated animate__bounce'].join(' ')} onAnimationEnd={() => this.animationend()}>这是一个基于CSS3的复合CSS动画</div>
                </div>
            </div>
        )
    }
}

export default About;
