import React, { useState } from 'react';
import logo from './logo.svg';
import './App.css';
import Header from './components/Header';
import Welcome from './components/Welcome'
import ColorSelector from './components/ColorSelector'

import { Route, Link } from "react-router-dom"
import About from './pages/about';
import TicTacToe from './components/TicTacToe';
import ChmodCalcular from './components/chmod-calcular';
import WalmartLine from './components/WalmartLine';
import OwnService from './pages/OwnService';
import Components from './pages/Components';
import PlayGround from './pages/Playground';

import AppBar from '@material-ui/core/AppBar';
import Button from '@material-ui/core/Button';
//import CameraIcon from '@material-ui/icons/PhotoCamera';
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from '@material-ui/core/Grid';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import { isValidVar } from 'utility-js';
import { Modal } from '@material-ui/core';

// 这个组件专门使用了不一致的构建方式
export default function Main() {
  const [state, setState] = useState({ hex: 'd7e0d0', rgb: 'rgb(215,224,208)' });
  // const [open, setOpen, hex, setHex, rgb, setRgb] = React.useState(false, "d7e0d0", "rgb(215,224,208)");
  const [open, setOpen] = React.useState(false);
  // 单个状态(只能用这俩常量去访问和设置，不能用state.xxx访问或者setState设置)
  const [contentBgColor, setContentBgColor] = React.useState("#ffffff");

  function Copyright() {
    return (
      <Typography variant="body2" color="textSecondary" align="center">
        {'Copyright © '}
        <Link color="inherit" to="https://material-ui.com/">
          Your Website
        </Link>{' '}
        {new Date().getFullYear()}
        {'.'}
      </Typography>
    );
  }

  const setBackgroundColor = (event) => {
    // setState({ hex: state.hex, rgb: state.rgb, contentBgColor: "#" + state.hex });
    const hex = trimHex(state.hex);
    if (isValidVar(hex)) {
      setContentBgColor("#" + hex);
      console.debug("设置背景: " + contentBgColor)
    } else {
      handleOpen();
    }
  }

  let useStyles = makeStyles((theme) => ({
    icon: {
      marginRight: theme.spacing(2),
    },
    heroContent: {
      backgroundColor: contentBgColor,
      // zIndex: 0, 
      // position: "relative",
      padding: theme.spacing(8, 0, 6),
    },
    heroButtons: {
      marginTop: theme.spacing(4),
    },
    footer: {
      backgroundColor: theme.palette.background.paper,
      padding: theme.spacing(6),
    },
    tipsText: {
      color: state.rgb,
      border: "2px solid #09f",
      backgroundColor: "aliceblue",
    },
    errorText: {
      color: "black",
      border: "2px solid red",
      backgroundColor: "#FFB6C1",
    },
  }));
  const classes = useStyles();

  const trimHex = (hex) => {
    if (!isValidVar(hex)) {
      hex = "";
    }

    if (hex.length < 6) {
      if (hex.length === 3) { // 处理 "#abc" 成 "#aabbcc"
        hex = hex.replace(/(.)/g, '$1$1');
      } else {
        hex = null;
      }
    } else if (hex.length === 6) {
      // DNT
    }
    else if (hex.length === 7 && hex[0] === "#") {
      hex = hex.substr(1);//去除前缀 # 号
    }
    else {
      hex = null;
    }
    return hex;
  }

  // 十六进制颜色值转换为RGB
  const hexToRgb = function (hex) {
    var rgb = [];
    hex = trimHex(hex);
    if(!isValidVar(hex)){
      return null;
    }

    hex.replace(/../g, function (color) {
      rgb.push(parseInt(color, 0x10));//按16进制将字符串转换为数字
    });

    return "rgb(" + rgb.join(",") + ")";
  };

  const onClicColorStranslateBt = () => {
    let rgb = hexToRgb(state.hex);
    console.debug("转换后的rgb: " + rgb)
    if (isValidVar(rgb)) {
      setState({ rgb: rgb, hex: state.hex });
      console.debug("设置rgb: " + rgb)
    } else {
      handleOpen();
    }
  }

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div className="App">
      <CssBaseline />
      <AppBar position="relative">
        <Toolbar className="App-toobar">
          {/* <CameraIcon className={classes.icon} /> */}
          <img src={logo} className="App-logo" alt="logo" />
          <Header></Header>
        </Toolbar>
      </AppBar>
      <main>
        {/* Hero unit */}
        <div className={classes.heroContent}>
          <Container>
            <Typography component="h1" variant="h2" align="center" color="textPrimary" gutterBottom>
              Guide
            </Typography>
            <header className="App-header">
              {/* <p>Edit <code>src/App.js</code> and save to reload.</p> */}
              {/* <a
              className="App-link"
              href="https://reactjs.org"
              target="_blank"
              rel="noopener noreferrer"
              >
              Learn React
            </a> */}
            </header>
            <Welcome name="来访者, 这是使用React开发的WEBAPP" />

            <Typography variant="h5" align="center" className={classes.tipsText} paragraph>
              这是转换后的颜色值: {state.rgb}
            </Typography>
            <Modal
              open={open}
              onClose={handleClose}
              aria-labelledby="simple-modal-title"
              aria-describedby="simple-modal-description"
            >
              <Typography variant="h5" align="center" className={classes.errorText} paragraph>
                十六进制值有误
            </Typography>
            </Modal>
            {/* 应该是有直接绑定的方法的, */}
            <input type="text" placeholder="此处输入16进制颜色" onChange={(event) => { setState({ hex: event.target.value, rgb: state.rgb }); }} defaultValue={state.hex} />
            <div className={classes.heroButtons}>
              颜色转换
              <Grid container spacing={2} justify="center">
                <Grid item>
                  <Button variant="contained" onClick={onClicColorStranslateBt} color="primary">
                    转换并设置文字颜色
                  </Button>
                </Grid>
                <Grid item>
                  <Button variant="outlined" color="primary" onClick={setBackgroundColor} >
                    设置背景色
                  </Button>
                </Grid>
              </Grid>
            </div>

            <br />
            <Route path='/colorselector' component={ColorSelector}></Route>
            <Route path='/WalmartLine' component={WalmartLine}></Route>
            <Route path='/chmodcalcular' component={ChmodCalcular}></Route>
            <Route path='/Components' component={Components}></Route>
            <Route path='/tictactoe' component={TicTacToe}></Route>
            <Route path='/ownservice' component={OwnService}></Route>
            <Route path='/playgroud' component={PlayGround}></Route>
            <Route path='/about' component={About}></Route>
            <br />

          </Container>

        </div>
      </main>
      {/* Footer */}
      <footer className={classes.footer}>
        <Typography variant="h6" align="center" gutterBottom>
          Historier
        </Typography>
        <Typography variant="subtitle1" align="center" color="textSecondary" component="p">
          Something here to give the footer a purpose!
        </Typography>
        <Copyright />
      </footer>
      {/* End footer */}
    </div >
  );
}
