import React from 'react'
import './chmod-calcular.css'
import Grid from '@material-ui/core/Grid';
import Checkbox from '@material-ui/core/Checkbox';
import StickyHeadTable from './StickyHeadTable';
import { PowerMode } from "../class/PowerMode";

class ChmodCalcular extends React.Component {

    // 使用ES6 class语法创建组件，初始化state的工作要在constructor中完成。不需要再调用getInitialState方法。
    constructor(props) {
        super(props)
        this.state = {
            visible: false,
            content: 1,
            this_data : [new PowerMode("read(4)", 4, true, 'violet'), new PowerMode("write(2)", 2, true, 'red'), new PowerMode("execute(1)", 1, true, 'yellow')],
            output : {
                owner: 7,
                group: 7,
                other: 7,
            },
        }
        // 使用ES6 class语法创建组件， class中的方法不会自动将this绑定到实例中。必须使用 .bind(this)或者 箭头函数 ＝>来进行手动绑定。
        this.show = this.show.bind(this);
    }

    show = () => {
        this.setState({
            visible: true,
        })
    }

    handleOk = () => {
        this.setState({
            visible: false,
            content: this.state.content + 1,
        })
        console.log('clicked ok!')
    }

    handleCancel = () => {
        this.setState({
            visible: false,
            content: this.state.content + 1,
        })
        console.log('clicked cancel')
    }

    renderGroupItem = d => {
        // console.log(d);
        const style = { borderBottom: `solid 1px ${d.color}`, paddingBottom: 2 }
        return <span style={style}>{d.value}</span>
    }

    handleTbCheckboxClick = (e, data) => {
        let {this_data} = this.state;
        
        // console.log(e.target);
        let checked = e.target.checked, value = e.target.id;
        
        this_data.forEach(power => {
            power.nameMapHost.forEach(host => {
                if(host.value.toString() === value.toString()){
                    host.isCheck = checked;
                }
            })
        });

        // 返回指定宿主名称对应权限的和
        const calculateModeBit = (name) => {
            // let {this_data} = this.state;
            let host = 0, result = 0;
            //console.log("name:" + name);
            for (let i = 0; i < this_data.length; ++i) {
                host = this_data[i].nameMapHost.get(name);
                if(host.value === value){
                    host.isCheck = checked;
                }
                result += host.isCheck ? host.value : 0;
                //console.log(`${this_data[i].name}, hoist-value=${host.value}, 选中:${host.isCheck}`);
            }
            //console.log("");
            return result;
        }

        let output = {
            owner: calculateModeBit('owner') / 100,
            group: calculateModeBit('group') / 10,
            other: calculateModeBit('other') / 1,
        };
        this.setState({
            // 修改复选框状态
            this_data : this_data,
            // 修改输出状态
            output : output,
        });
    }

    render() {
        const {this_data, output} = this.state;

        const style = {
            color: '#fff',
            lineHeight: '30px',
            textAlign: 'center',
            background: '#c2cbdd',
        }
        const columns = [
            { label: '权限\\宿主', render: d => d.name },
            // type: 'checkbox', 只会选中这一行
            // Checkbox的checked属性会选中并且不可编辑(所谓的受控组件)
            // <input name="owner" type="checkbox" id={e} checked={e.isCheck} onChange={this.handleTbCheckboxClick}/> }},
            
            // 这个组件封装得过于简单了 未选中时获取不了value
            // 选中时，value 为 htmlValue，checked 为 true;
            // 未选中时，value 为 undefined，checked 为 false; 这导致了不能用这玩意进行比较啊 shit
            // <Checkbox keygen="value" data={e} checked={e.isCheck} format="name" value={e.value} htmlValue={e.value} onChange={this.handleTbCheckboxClick} /> }},
            { label: 'owner', render: d => { let e = d.nameMapHost.get('owner'); return <input name="owner" type="checkbox" id={e.value} checked={e.isCheck} onChange={this.handleTbCheckboxClick}/> }},
            { label: 'group', render: d => { let e = d.nameMapHost.get('group'); return <input name="group" type="checkbox" id={e.value} checked={e.isCheck} onChange={this.handleTbCheckboxClick}/> }},
            { label: 'other', render: d => { let e = d.nameMapHost.get('other'); return <input name="other" type="checkbox" id={e.value} checked={e.isCheck} onChange={this.handleTbCheckboxClick}/> }},
        ]
        const rowClassName = d => {
            return 'modes';
        }
        const getPowerData = (i, hostName) => {
            return this_data[i].nameMapHost.get(hostName);
        }

        return (
            <div>
                <Checkbox >{'value is "ok"'}</Checkbox>
                <h1 className="title color-aqua">chmod数值计算器</h1>
                <p className="description">快速创建8进制chmod权限</p>
                <br /> <span className='title'>Table实现</span>
                <div className="calc modes" width={500}>
                    {/* width={500}  */}
                    <StickyHeadTable className="myTable" rowClassName={rowClassName} keygen="name" columns={columns} rows={this_data} />
                </div>
                <br /> Grid实现
                <div className="calc">
                    {/* 表头 */}
                    <Grid container style={{ textAlign: 'center' }}>
                        <Grid style={style} item xs={3}>
                            权限名
                        </Grid>
                        <Grid style={{ ...style, background: '#f5f5f5', color: '#333' }} item xs={12} >
                            宿主
                        </Grid>
                    </Grid>
                    {/* 首行 */}
                    <Grid container style={{ textAlign: 'center' }}>
                        <Grid item xs={3} style={style}>-</Grid>
                        <Grid style={{ ...style, background: '#f5f5f5', color: '#333' }} item xs={3}>
                            Owner
                        </Grid>
                        <Grid style={{ ...style, background: '#f5f5f5', color: '#333' }} item xs={3}>
                            Group
                        </Grid>
                        <Grid style={{ ...style, background: '#f5f5f5', color: '#333' }} item xs={3}>
                            Other
                        </Grid>
                    </Grid>
                    {/* 数据行1 */}
                    <Grid container style={{ textAlign: 'center' }}>
                        {/* defaultValue={['read', 'write']} */}
                        <Grid item xs={3} style={style}>Read (4)</Grid>
                        <Grid item xs={3}><input name="owner" type="checkbox" id={getPowerData(0, 'owner').value} checked={getPowerData(0, 'owner').isCheck} onChange={this.handleTbCheckboxClick}/>{getPowerData(0, 'owner').value}</Grid>
                        <Grid item xs={3}><input name="group" type="checkbox" id={getPowerData(0, 'group').value} checked={getPowerData(0, 'group').isCheck} onChange={this.handleTbCheckboxClick}/>{getPowerData(0, 'group').value}</Grid>
                        <Grid item xs={3}><input name="other" type="checkbox" id={getPowerData(0, 'other').value} checked={getPowerData(0, 'other').isCheck} onChange={this.handleTbCheckboxClick}/>{getPowerData(0, 'other').value}</Grid>
                    </Grid>
                    <Grid container style={{ textAlign: 'center' }}>
                        <Grid item xs={3} style={style}>Write (2)</Grid>
                        <Grid item xs={3}><input name="owner" type="checkbox" id={getPowerData(1, 'owner').value} checked={getPowerData(1, 'owner').isCheck} onChange={this.handleTbCheckboxClick}/>{getPowerData(1, 'owner').value}</Grid>
                        <Grid item xs={3}><input name="group" type="checkbox" id={getPowerData(1, 'group').value} checked={getPowerData(1, 'group').isCheck} onChange={this.handleTbCheckboxClick}/>{getPowerData(1, 'group').value}</Grid>
                        <Grid item xs={3}><input name="other" type="checkbox" id={getPowerData(1, 'other').value} checked={getPowerData(1, 'other').isCheck} onChange={this.handleTbCheckboxClick}/>{getPowerData(1, 'other').value}</Grid>
                    </Grid>
                    <Grid container style={{ textAlign: 'center' }}>
                        <Grid item xs={3} style={style}>Execute (1)</Grid>
                        <Grid item xs={3}><input name="owner" type="checkbox" id={getPowerData(2, 'owner').value} checked={getPowerData(2, 'owner').isCheck} onChange={this.handleTbCheckboxClick}/>{getPowerData(2, 'owner').value}</Grid>
                        <Grid item xs={3}><input name="group" type="checkbox" id={getPowerData(2, 'group').value} checked={getPowerData(2, 'group').isCheck} onChange={this.handleTbCheckboxClick}/>{getPowerData(2, 'group').value}</Grid>
                        <Grid item xs={3}><input name="other" type="checkbox" id={getPowerData(2, 'other').value} checked={getPowerData(2, 'other').isCheck} onChange={this.handleTbCheckboxClick}/>{getPowerData(2, 'other').value}</Grid>
                    </Grid>
                </div>
                <div className="calc results">
                    <div className="result spacer">Result</div>
                    <div className="result owner">{output.owner}</div>
                    <div className="result group">{output.group}</div>
                    <div className="result other">{output.other}</div>
                </div>
            </div>
        )
    }
}

export default ChmodCalcular;
