import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import ListSubheader from '@material-ui/core/ListSubheader';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Collapse from '@material-ui/core/Collapse';
import InboxIcon from '@material-ui/icons/MoveToInbox';
// import DraftsIcon from '@material-ui/icons/Drafts';
// import SendIcon from '@material-ui/icons/Send';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import StarBorder from '@material-ui/icons/StarBorder';

import { FixedSizeList } from 'react-window';

// import groupList from "./group.json";

export function ListItemLink(props) {
    return <ListItem button component="a" {...props} />;
}

// 多级列表版 （尽管使用了定高列表 但是还是会拉长页面 体验不好）
export function NestedList(props) {
     let groupList = props.groupList;

    const useStyles = makeStyles((theme) => ({
        root: {
            left: '10%',
            width: '100%',
            maxWidth: '80%',
            backgroundColor: theme.palette.background.paper,
        },
        nested: {
            paddingLeft: theme.spacing(4),
        },
    }));

    const classes = useStyles();
    const [open, setOpen] = React.useState([]);

    const handleClick = (i) => {
        return () => {
            open[i] = !open[i];
            // 必须拷贝一份它才认为状态发生了变动
            setOpen(Array.from(open));
            // setOpen(open); // invalid
        }
    };

    return (
        <List
            component="nav"
            aria-labelledby="nested-list-subheader"
            subheader={
                <ListSubheader component="div" id="nested-list-subheader">
                    多级列表
                </ListSubheader>
            }
            className={classes.root}
        >
            {
                groupList.map((group, r) => (
                    <div key={r}>
                        <ListItem button onClick={handleClick(r)}>
                            <ListItemIcon>
                                <InboxIcon />
                            </ListItemIcon>
                            <ListItemText primary={group.name} />
                            {open[r] ? <ExpandLess /> : <ExpandMore />}
                        </ListItem>
                        <Collapse in={open[r]} timeout="auto" unmountOnExit>
                            <FixedSizeList height={200} itemSize={50} itemCount={group.elements.length}>
                                {
                                    (props) => {
                                        const { index, style } = props;
                                        let ele = group.elements[index];
                                        return (
                                            <ListItem button style={style} key={index} className={classes.nested}>
                                                <ListItemIcon>
                                                    <StarBorder />
                                                </ListItemIcon>
                                                <ListItemLink className="single-line-text-overflow-omitted" href={ele.href}>
                                                    <ListItemText  primary={`${ele.name}`} />
                                                </ListItemLink>
                                            </ListItem>
                                        );
                                    }
                                }
                            </FixedSizeList>
                        </Collapse>
                    </div>
                ))
            }
        </List>
    );
}
