import React from 'react';
// import { ResponsiveContainer, LineChart, Line, XAxis, YAxis, ReferenceLine, ReferenceArea,
//     ReferenceDot, Tooltip, CartesianGrid, Legend, Brush, ErrorBar, AreaChart, Area,
//     Label, LabelList } from 'recharts';
import { LineChart, Line, Legend, XAxis, YAxis, Tooltip, CartesianGrid } from 'recharts';
// import { ResponsiveContainer, BarChart, Bar } from 'recharts';
// import * as _ from 'lodash';
// import {SheetJSApp} from './/sheetjs';
import ReactFileReader from 'react-file-reader';
import Papa from 'papaparse';
import _ from 'lodash';
import colorConverter from 'color-convert'
import '../css/ood.css'

import { create, all } from 'mathjs'

// create a mathjs instance with configuration
const config = {
    epsilon: 1e-12,
    matrix: 'Matrix',
    number: 'number',
    precision: 64,
    predictable: false,
    randomSeed: null
}
const math = create(all, config)

// import csv_data from '../data/end-of-2020-11-08.csv';
// // csv_data :/static/media/end-of-2020-11-08.a0de207b.csv
// console.debug("csv_data :" + csv_data);
// console.debug("colorList :");
// console.debug(colorList[0].obj);


export default function WalmartLine() {
    const bd = document.querySelector('body');
    const scrWidth = bd.offsetWidth;

    const [csvRenderData, setCsvRenderData] = React.useState({});
    const [csvTupleNameList, setCsvTupleNameList] = React.useState([]);
    const [legendList, setLegendList] = React.useState([]);
    const [hideTupleNameList, setHideTupleNameList] = React.useState([]);
    const [keyMapColorHexCache] = React.useState(new Map());
    // 最大显示的数据维度数
    const [maxDim, setMaxDim] = React.useState(10);
    const [curDim, setCurDim] = React.useState(3);
    // const [pastFileData, setPastFileData] = React.useState(null);
    // const [colorIndex, setColorIndex] = React.useState(0);
    let colorIndex = 0;
    const setColorIndex = (i) => { colorIndex = i }
    // const [header, setHeader] = React.useState([]);
    // let legendList = [];
    // let hideTupleNameList = [];
    let renderDataArr = [];
    let renderTupleNameMapData = new Map();
    let renderXMapTuple = new Map();

    const setData = (result) => {
        console.debug("result: ", result);
        if (_.isNull(result)) {
            return;
        }
        // 重置隐藏
        setHideTupleNameList([]);
        // setPastFileData(result);
        // console.log("csv 已解析: " + JSON.stringify(result.data));
        const headerMeta = result.data.splice(0, 1)[0];
        console.debug(headerMeta);
        let csvDataBuffer = [];
        let cur_order = "";
        const calcCateAndName = (name) => {
            let arr = name.split("*");
            arr = arr.filter(ele => !_.isEmpty(ele));
            if (arr.length > 2) {
                console.error("数据有问题");
            }
            return arr;
        }
        result.data.forEach(ele => {
            if (ele.length >= headerMeta.length) {
                let arr = calcCateAndName(ele[1]);
                csvDataBuffer.push({
                    "order": cur_order,
                    "num": cur_order + "-" + ele[0],
                    "tuple_name": arr[0],
                    "category": arr[0],
                    "goods_name": arr[1],
                    "name": ele[1],
                    "sku": ele[2],
                    "unit": ele[3],
                    "count": ele[4],
                    "price": ele[5],
                    "amount": ele[6],
                    "tax_rate": ele[7],
                    "tax_amount": ele[8],
                });
            } else {
                cur_order = ele[0];
            }
        });
        console.debug("csvDataBuffer: ", csvDataBuffer);
        csvDataBuffer.forEach((ele) => {
            let tuple = renderTupleNameMapData.get(ele.tuple_name);
            if (_.isEmpty(tuple)) {
                renderTupleNameMapData.set(ele.tuple_name, new Array(ele));
            } else {
                tuple.push(ele);
            }
        })
        csvDataBuffer.forEach((ele) => {
            let tuple = renderXMapTuple.get(ele.order);
            if (_.isEmpty(tuple)) {
                renderXMapTuple.set(ele.order, new Array(ele));
            } else {
                tuple.push(ele);
            }
        })
        setMaxDim(renderTupleNameMapData.size);
        console.debug("renderTupleNameMapData: ", renderTupleNameMapData);
        console.debug("renderXMapTuple: ", renderXMapTuple);
        let tupleNameListBuffer = [];
        let rks = renderTupleNameMapData.keys();
        // iterator只能迭代一次
        for (let kn of rks) {
            tupleNameListBuffer.push(kn);
        }
        // 只保留前n个数据维度
        console.debug("setData-curDim", curDim)
        console.debug("tupleNameListBuffer: ", tupleNameListBuffer);

        let order_index = 0;
        renderXMapTuple.forEach((values, key) => {
            let renderDataTuple = {};
            tupleNameListBuffer.forEach(tupleName => renderDataTuple[tupleName] = 0);
            renderDataTuple["x"] = key;
            // renderDataTuple["x"] = renderDataTuple["x"].replace("订单号[", "")
            // renderDataTuple["x"] = renderDataTuple["x"].replace("]", "")
            // renderDataTuple["x"] = renderDataTuple["x"].replace("807032000", "")
            // 不然太长了根本显示不出来
            renderDataTuple["x"] = ++order_index;
            values.forEach(value => {
                // renderDataTuple[value.tuple_name] = _.toNumber(value.price);
                // renderDataTuple[value.tuple_name] = renderDataTuple[value.tuple_name] + _.toNumber(value.price);
                // 解决计算精度问题 第14单 0.71 + 1.27 + 3.19 + 7.1 + 1.05 + 1.5 = 18.78 但js计算结果是14.82，用前面的公式计算结果是 18.779999999999998
                // 4.67 + 1.27 = 5.9399999999999995
                const curValue = renderDataTuple[value.tuple_name];
                let plusValue = _.toNumber(value.price);
                if (_.isNaN(plusValue)) {
                    // 视为打折商品处理，此时这个是折扣额
                    // plusValue = _.toNumber(value.amount);
                    // 但是price是单价诶，要用总价去处理打折的，算了，直接忽略就好
                    plusValue = 0;
                    if (_.isNaN(plusValue)) {
                        // 还不对就抛异常
                        console.error("数据不正确");
                    }
                }
                renderDataTuple[value.tuple_name] = math.add(_.toNumber(curValue), plusValue).toString();
                console.debug(renderDataTuple["x"], key, value.tuple_name, curValue, "+", plusValue, "=", renderDataTuple[value.tuple_name])
            })
            values.forEach(value => {
                // 用14位精度控制最终显示
                renderDataTuple[value.tuple_name] = _.toNumber(math.format(_.toNumber(renderDataTuple[value.tuple_name]), { precision: 16 }));
            })
            renderDataArr.push(renderDataTuple);
        })
        // 不能在得到 renderXMapTuple 之前剪切维度
        tupleNameListBuffer = tupleNameListBuffer.slice(0, curDim)
        console.debug("renderDataArr: ", renderDataArr);

        setCsvRenderData(renderDataArr);
        setCsvTupleNameList(tupleNameListBuffer);

        // legendList 和 csvTupleNameList 要分开 前者只在数据装载时计算一次
        let legendListBuffer = [];
        // let csvTupleNameListBuffer = csvTupleNameList.filter(ele => !hideTupleNameListBuffer.includes(ele))
        // csvTupleNameList.forEach((ele, index) => {
        tupleNameListBuffer.forEach((tupleName, index) => {
            legendListBuffer.push({
                "value": tupleName,
                "type": "line",
                "id": index
            });
        });
        setLegendList(legendListBuffer);
        console.debug("legendListBuffer");
        console.debug(legendListBuffer);
    }
    const handleFiles = files => {
        Papa.parse(files[0], { complete: setData });
    }

    const padding0 = (num, length) => {
        num = num + "";
        for (var len = num.length; len < length; len = num.length) {
            num = "0" + num;
        }
        return num;
    }

    const numPadding0Reverse = (num, count) => {
        return padding0(num, count).split("").reverse().join("")
    }

    const getColorHex = (key) => {
        // console.debug("keyMapColorHexCache: ", keyMapColorHexCache);
        let colorHex = keyMapColorHexCache.get(key);
        console.debug("key: ", key, "; 颜色cache: ", colorHex);
        if (_.isUndefined(colorHex)) {
            colorHex = "#" + colorConverter.hsl.hex(numPadding0Reverse(colorIndex, 3) % 360, 100, 50);
            setColorIndex(colorIndex + 1);
            console.debug("新建颜色_" + colorIndex, colorHex);
            keyMapColorHexCache.set(key, colorHex);
        }
        if (hideTupleNameList.includes(key)) {
            // 如果是隐藏的，就返回浅色（前面的算法不会生成浅色）
            // colorHex = "#F5F5DC" // 浅褐色，太浅了，当背景还行
            colorHex = "#" + colorConverter.rgb.hex(119, 117, 117);
        }
        return colorHex;
    }

    const onClickLegend = (e) => {
        let csvTupleNameListBuffer = csvTupleNameList.slice(0);
        let hideTupleNameListBuffer = hideTupleNameList.slice(0);

        // 不同组件这个有区别，和onClick的绑定位置有关(其实自己设定一个attr也行)，可能是，.dataKey, .value
        // e.target.innerText 是含有空格的
        console.debug(e.target.attributes);
        let value = e.target.attributes["value"].nodeValue;

        let index = hideTupleNameListBuffer.indexOf(value);
        if (index >= 0) {
            // console.debug("删除隐藏: ", index);
            console.debug("显示: \"", value, "\"");
            hideTupleNameListBuffer.splice(index, 1);
            csvTupleNameListBuffer.push(value);
        } else {
            console.debug("隐藏: \"", value + "\"");
            hideTupleNameListBuffer.push(value);
            csvTupleNameListBuffer.splice(csvTupleNameListBuffer.indexOf(value), 1);
        }
        // csvTupleNameListBuffer = csvTupleNameList.filter(ele => !hideTupleNameListBuffer.includes(ele))
        setHideTupleNameList(hideTupleNameListBuffer);
        setCsvTupleNameList(csvTupleNameListBuffer);

        // console.debug("hideTupleNameList");
        // console.debug(hideTupleNameList);
        // console.debug("csvTupleNameList");
        // console.debug(csvTupleNameList);
    }

    const renderLegend = (props) => {
        const { payload } = props;
        console.debug("renderLegend")
        return (
            <>
                {
                    payload.map((legendEntry, index) => {
                        let colorHex = getColorHex(legendEntry.value);
                        return (
                            <span
                                onClick={onClickLegend}
                                key={`item-${index}`}
                                className="forbidden-select"
                                value={legendEntry.value}
                                style={{ cursor: "pointer", padding: 5, color: colorHex }}>
                                {legendEntry.value}
                            </span>
                        )
                    })
                }
            </>
        );
    }

    return (
        <>
            {/* <ResponsiveContainer width="100%" height="100%"></ResponsiveContainer> */}
            <LineChart
                width={scrWidth / 1.5}
                height={450}
                data={(() => { console.debug("lineChart-curDim", curDim); return csvRenderData })()}
                margin={{ top: 5, right: 20, left: 10, bottom: 5 }}
            // onMouseDown={(e) => console.debug("onMouseDown", e.activeLabel)} // 按下
            // onMouseMove={(e) => console.debug("onMouseMove", e.activeLabel) } // 移动
            // onMouseUp={() => console.debug("MouseUp")} // 松开
            >
                {/* 浮窗提示信息 */}
                <Tooltip />
                <XAxis allowDataOverflow={true} dataKey="x" domain={['auto', 'auto']} />
                {/* Y轴  interval={"preserveStartEnd"} tickCount={10} minTickGapNumber={1} allowDataOverflow={true}  */}
                {/* 坑，这个字符串里面不能写 + 8 的，不然偶尔会出现 189999999999998，这个库的文档就问题很多 */}
                <YAxis unit={"￥"} domain={['0', 'dataMax']} type="number" />
                {/* 图例 */}
                <Legend payload={legendList} content={renderLegend} />
                {/* 网格背景 */}
                <CartesianGrid stroke="#f5f5f5" />
                {
                    csvTupleNameList.map((tupleName, index) => {
                        console.debug("Line-tupleName", tupleName)
                        // label={{ fill: 'red', fontSize: 20 }}
                        // fill={index === activeIndex ? '#82ca9d' : '#8884d8'}
                        // 拿不到key啊
                        //  onMouseOver={(props, event) => { let { attributes } = event.target; console.debug("选中", attributes) }} // 越过
                        return (
                            <Line
                                onMouseDown={(e) => console.debug("onMouseDown", e)}
                                // fill={index}
                                key={index.toString()}
                                type="monotone"
                                dataKey={tupleName}
                                stroke={getColorHex(tupleName)}
                            />
                        )
                    })
                }
                {/* <Line type="monotone" dataKey="*塑料制品*塑料购物袋" stroke="#ff7300" yAxisId={100} /> */}
            </LineChart>
            {/* <BarChart width={600} height={300} data={csvRenderData}>
                <XAxis dataKey="tuple_name" stroke="#8884d8" />
                <YAxis />
                <Tooltip />
                <CartesianGrid stroke="#ccc" strokeDasharray="5 5" />
                <Bar dataKey="*塑料制品*塑料购物袋" fill="#8884d8" barSize={30} />
            </BarChart> */}
            {/* 看样子就是没法动态，需要展示的数据维度只能硬编码 */}
            {/* <Line type="monotone" points={[{"x" : 1, "y" : 2, "value" : 2}, {"x" : 2, "y" : 10, "value" : 10}]} dataKey="value" stroke="#ff7300" /> */}
            <ReactFileReader handleFiles={handleFiles} fileTypes={'.csv'}>
                <button className='btn' style={{ cursor: "pointer" }} >
                    上载CSV数据
                </button>
            </ReactFileReader>
            <br />
            最大展示数据维度(建议不超过10): {curDim}
            <br />
            <input
                type="range"
                onChange={(e) => {
                    setCurDim(e.target.value);
                    console.debug("input-curDim", curDim);
                    // setData(pastFileData);
                }}
                defaultValue={curDim}
                min="1" max={maxDim} placeholder="curDim"
            />
        </>
    )
}