import React from 'react';
// import { Link } from "react-router-dom"
import { VerticalTabs } from '../components/VerticalTabs';
import { NestedList } from '../components/NestedList';
import { TwoDimGrids } from '../components/TwoDimGrids';

// 正规Json文件 属性名要用引号括起来 最后一个数组元素或者属性后不能跟逗号
// 但是直接定义带js里面的对象可以忽略
import groupList from "../data/group.json";
const { isValidVar } = require("utility-js");
//console.log(groupList);

// 当前展示等级，高于此等级的项目并不会被展示出来
const curShowLv = 2;

let renderGroupList = groupList.filter((group) => {
  // 筛除满足这些条件的元素
  if (isValidVar(group.show_lv) && group.show_lv > curShowLv) {
    // console.debug(group.name + "已隐藏,显示等级:" + group.show_lv);
    // groupList.splice(r, 1);
    return false;
  } else {
    group.elements = group.elements.filter((ele) => {
      // 隐藏项目要在定高列表中实现，但是仅仅隐藏的话高度并不会减少，所以预先筛掉更加方便
      if (isValidVar(ele.show_lv) && ele.show_lv > curShowLv) {
        // console.debug(ele.name + "已隐藏,显示等级:" + ele.show_lv);
        return false;
      }
      return true;
    })
    return true;
  }
});
export default function OwnService() {
  return (
    <>
      <VerticalTabs renderGroupList={renderGroupList} />
      <br />
      <TwoDimGrids renderGroupList={renderGroupList} />
      <NestedList groupList={renderGroupList} />
    </>
  )
}
