import React from 'react'
import '../css/pass-reset.css'

class PassInputBox extends React.Component {

    // 使用ES6 class语法创建组件，初始化state的工作要在constructor中完成。不需要再调用getInitialState方法。
    constructor(props) {
        super(props)
        this.state = {
            visible: false,
        }
        // 使用ES6 class语法创建组件， class中的方法不会自动将this绑定到实例中。必须使用 .bind(this)或者 箭头函数 ＝>来进行手动绑定。
        this.show = this.show.bind(this);
    }

    show = () => {
        this.setState({
            visible: true,
        })
    }

    onChange = (event) => {
        this.setState({value: event.target.value})
        console.log("change事件：" + this.state.value)
    }

    render() {
        return (
            <div className="input-body">
                <div className="main">
                    <form>
                        <div className="label-2">
                            <input type="text" className="text" defaultValue="" placeholder="请输入注册邮箱" onChange={this.onChange}  />
                        </div>
                        <div className="clear"></div>
                        <div className="submit">
                            <input type="submit" value="重置密码" />
                        </div>
                        <div className="clear"></div>
                    </form>
                </div>
            </div>
        )
    }
}

export default PassInputBox;
